body{
    /* background-color: rgb(188, 181, 236); */
    background-color: #939597;
    /* background-color: #E0B589; */
    
}
.App{
    display: flex;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
}
.appHeader{
    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 1px 2px 2px #00000033;
}
.soppingCartIcon{
    font-size: 30px;
    margin-right: 30px;
    padding-left: 300px;
    cursor: pointer;
}
.productsContainer{
    padding: 8px;
   
}
.appMenu{
    border: none;

}
.pageContent{
    display: flex;
    flex: 1;
    overflow-y: auto;
    margin-top: 2px;

}
.appFooter{
    height: 60px;
    background-color: rgb(0, 0, 0);
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}
.itemCard, .itemCardBadge{
    margin: 8px;
    background-color: wheat
}
.itemCardImage{
    height: 150px !important;
    object-fit: scale-down;
}